import React from 'react';
import './nav.css';
import logo from '../imgs/logo.svg';
import {Link} from 'react-router-dom';

function Nav() {
	return (
		<div className='navBar'>
			<img className='logo' src={logo}></img>
			<div className='login'>LOG IN</div>
		</div>
	);
}

export default Nav;